import panel from "../components/panel";
import { mapGetters, mapActions } from "vuex";

export default {
  components: {
    panel,
  },
  head() {
    return {
      title: "GlennPacker.net Clients",
      meta: [
        {
          hid: "description",
          name: "description",
          content: "glennpacker.net clients",
        },
      ],
    };
  },
  data() {
    return {
      panelColors: [
        "#ff711e",
        "#ffe11e",
        "#c71eff",
        "#ffe11e",
        "#c71eff",
        "#ff711e",
        "#c71eff",
        "#ff711e",
        "#ffe11e",
      ],
      vs: {
        showC1: false,
        showC2: false,
        other: {
          showC1: false,
          showC2: false,
          showC4: false,
          showC5: false,
          showC6: false,
          showC7: false,
          showC8: false,
          showC9: false,
        },
      },
    };
  },
  computed: {
    ...mapGetters({ clients: 'history/getClients' }),
    vueClients() {
      if (!this.clients) return null;
      return this.clients.filter((client) =>
        client.technologies.some((t) => t.text.toLowerCase().includes("vue"))
      );
    },
  },
  methods: {
    ...mapActions({ loadHistories: 'history/loadHistories' }),
    backEndItems(client) {
      const items = client.technologies.filter((r) => r.isBackEnd && !r.link);
      return items.map((item) => item.text).join(", ");
    },
    backEndLinks(client) {
      return client.technologies.filter((r) => r.isBackEnd && r.link);
    },
    frontEndItems(client) {
      const items = client.technologies.filter((r) => !r.isBackEnd && !r.link);
      return items.map((item) => item.text).join(", ");
    },
    frontEndLinks(client) {
      return client.technologies.filter((r) => !r.isBackEnd && r.link);
    },
    hasBackEnd(client) {
      if (!client) return [];
      return client.technologies.some((r) => r.isBackEnd);
    },
    hasFrontEnd(client) {
      if (!client) return [];
      return client.technologies.some((r) => !r.isBackEnd);
    },
  },
  created() {
    this.loadHistories();
  },
};
